const triggerClassName = 'cardsTrigger';

const useStyles = createUseStyles({
    competences: {
        composes: 'flexColumn alignCenter stretchSelf',
        margin: [100, 0],
    },
    plxTrigger: {
        marginTop: '30vh',
        pointerEvents: 'none',
    },
    cardsPlx: {
        composes: 'flexRow center',
        width: '100%',
        pointerEvents: 'none',
        bottom: 0,
        opacity: 0,
        position: 'fixed',
    },
    scrollY: {
        marginTop: '480vh',
        pointerEvents: 'none',
    },
});

const Competences: FC = () => {
    const classes = useStyles();
    const [currentStep, setCurrentStep] = useState('');

    const handleCurrentStep = (name: string): void => {
        setCurrentStep(name);
    };

    const parallaxData = useMemo(
        () => [
            {
                start: `.${triggerClassName}`,
                duration: '50vh',
                properties: [
                    {
                        startValue: 40,
                        endValue: -15,
                        unit: 'vh',
                        property: 'translateY',
                    },
                    {
                        startValue: 0,
                        endValue: 1,
                        property: 'opacity',
                    },
                ],
            },
        ],
        []
    );

    return (
        <div className={classes.competences}>
            <div className={clsx(triggerClassName, classes.plxTrigger)} />
            <Plx parallaxData={parallaxData} className={classes.cardsPlx}>
                <CompetencesSvg current={currentStep} items={competenceItems} />
                <Cards triggerClassName={triggerClassName} items={competenceItems} onCurrentStep={handleCurrentStep} />
            </Plx>
            {/* ------ simulate the long scollY ------ */}
            <div className={classes.scrollY} />
        </div>
    );
};

export default Competences;
